<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="confirm" 
      :width="800"
    >
      <div slot="title">用户信息</div>
      <div class="modal-body">
        <h3>基本信息</h3>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">用户ID:</span>
            <span class="value" style="width: 630px;"> {{ form.id }} </span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">用户账号:</span>
            <a-input v-model="form.username" style="width: 630px;" />
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">用户邮箱:</span>
            <a-input v-model="form.email" style="width: 630px;" />
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">用户电话:</span>
            <a-input v-model="form.telephone_number" style="width: 630px;" />
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">活跃:</span>
            <a-radio-group v-model="form.is_active">
              <a-radio :value="true">是</a-radio>
              <a-radio :value="false">否</a-radio>
            </a-radio-group>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">系统管理员:</span>
            <a-radio-group v-model="form.is_admin">
              <a-radio :value="true">是</a-radio>
              <a-radio :value="false">否</a-radio>
            </a-radio-group>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">部门管理员:</span>
            <a-radio-group v-model="form.is_department_admin">
              <a-radio :value="true">是</a-radio>
              <a-radio :value="false">否</a-radio>
            </a-radio-group>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">小组管理员:</span>
            <a-radio-group v-model="form.is_team_admin">
              <a-radio :value="true">是</a-radio>
              <a-radio :value="false">否</a-radio>
            </a-radio-group>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">部门:</span>
            <a-select v-model="department_id" placeholder="请选择" style="width: 215px;" @change="department_change"> 
              <a-select-option v-for="option in department_options" :value="option.id" :key="option.id">{{ option.name }}</a-select-option> 
            </a-select> 
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">小组:</span>
            <a-select v-model="team_id" placeholder="请选择" style="width: 215px;"> 
              <a-select-option v-for="option in team_options" :value="option.id" :key="option.id">{{ option.name }}</a-select-option> 
            </a-select> 
          </div>
        </div>

        <h3 style="margin-top: 10px;">角色绑定</h3>
        <div v-for="(role, index) in roles" :key="role.id" style="display: flex;">
          <span style="margin-right: 8px; display: flex; ;align-items: center;">{{ parseInt(index)+1 }}.</span>
          <div>
            <div class="row">
              <a-select v-model="role.id" placeholder="请选择" style="width: 215px;"> 
                <a-select-option v-for="option in role_options" :value="option.id" :key="option.id">{{ option.name }}</a-select-option> 
              </a-select> 
              <a-button type="danger" icon="delete" size="small"  @click="() => delRoles(index)" style="margin-left: 15px; width: 100px;">删除</a-button>
            </div>
          </div>  
        </div>
        <a-button icon="edit" size="small" @click="addRoles" style="margin-left: 500px;">新增角色</a-button>

        <h3 style="margin-top: 10px;">店铺绑定</h3>
        <div v-for="(shop, index) in shops" :key="shop.id" style="display: flex;">
          <span style="margin-right: 8px; display: flex; ;align-items: center;">{{ parseInt(index)+1 }}.</span>
          <div>
            <div class="row">
              <a-select v-model="shop.id" placeholder="请选择" style="width: 215px;"> 
                <a-select-option v-for="option in shop_options" :value="option.id" :key="option.id">{{ option.name }}</a-select-option> 
              </a-select> 
              <a-button type="danger" icon="delete" size="small"  @click="() => delShops(index)" style="margin-left: 15px; width: 100px;">删除</a-button>
            </div>
          </div>  
        </div>
        <a-button icon="edit" size="small" @click="addShops" style="margin-left: 500px;">新增管理店铺</a-button>

        <h3 style="margin-top: 10px;">管理ozon订单小伙伴绑定</h3>
        <div v-for="(partner_orders, index) in partners_orders" :key="partner_orders.id" style="display: flex;">
          <span style="margin-right: 8px; display: flex; ;align-items: center;">{{ parseInt(index)+1 }}.</span>
          <div>
            <div class="row">
              <a-select v-model="partner_orders.id" placeholder="请选择" style="width: 215px;"> 
                <a-select-option v-for="option in user_options" :value="option.id" :key="option.id">{{ option.name }}</a-select-option> 
              </a-select> 
              <a-button type="danger" icon="delete" size="small"  @click="() => delPartnerOrders(index)" style="margin-left: 15px; width: 100px;">删除</a-button>
            </div>
          </div>  
        </div>
        <a-button icon="edit" size="small" @click="addPartnerOrders" style="margin-left: 500px;">新增ozon订单管理小伙伴</a-button>

        <h3 style="margin-top: 10px;">管理系统内商品小伙伴绑定</h3>
        <div v-for="(partner_system_products, index) in partners_system_products" :key="partner_system_products.name" style="display: flex;">
          <span style="margin-right: 8px; display: flex; ;align-items: center;">{{ parseInt(index)+1 }}.</span>
          <div>
            <div class="row">
              <a-select v-model="partner_system_products.id" placeholder="请选择" style="width: 215px;"> 
                <a-select-option v-for="option in user_options" :value="option.id" :key="option.id">{{ option.name }}</a-select-option> 
              </a-select> 
              <a-button type="danger" icon="delete" size="small"  @click="() => delPartnerSystemProducts(index)" style="margin-left: 15px; width: 100px;">删除</a-button>
            </div>
          </div>  
        </div>
        <a-button icon="edit" size="small" @click="addPartnerSystemProducts" style="margin-left: 500px;">新增管理系统内商品小伙伴</a-button>


        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;">上次登陆:</span>
            <span class="value">{{ form.last_login_time }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;" >创建时间:</span>
            <span class="value">{{ form.create_time }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label" style="width: 71px;" >修改时间:</span>
            <span class="value" style="width: 630px;">{{ form.modify_time }}</span>
          </div>
        </div>
    </div>
    </a-modal>
  </div>
</template>

<script>
import { getShopData,getUserData,getDepartmentData,getDataFromDepartment,adminModifyData } from '@/api/system'
import { message } from "ant-design-vue";

export default {
  name: 'FormModal',
  props: ['visible', 'form'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
      // 角色相关
      role_options: [
        {"id": "1","name":"运营"},
        {"id": "2","name":"采购"},
        {"id": "3","name":"打包"},
        {"id": "4","name":"财务"}
      ],
      roles:[],
      // 店铺相关
      shop_options:[],
      shops:[],
      // 用户相关
      user_options:[],
      partners_orders:[],
      partners_system_products:[],
      // 部门相关
      department_options:[],
      department_id: "",
      // 小组相关
      team_options:[],
      team_id: ""
    };
  },
  watch:{
    form:{
      handler(newValue){
        if(this.form.department){
          this.department_id = this.form.department.id
        }

        if(this.form.team){
          this.team_id = this.form.team.id
        }

        this.roles = this.form.roles.map(item => {
          return {id:item.id}
        })
        this.shops = this.form.shops.map(item => {
          return {id:item.id}
        })
        this.partners_orders = this.form.partners_orders.map(item => {
          return {id:item.id}
        })
        this.partners_system_products = this.form.partners_system_products.map(item => {
          return {id:item.id}
        })
        

        if (this.form.department.id) {
          getDataFromDepartment({"department_id":this.form.department.id}).then(
            res =>{
              this.team_options = res.data.data.map(item => {
                return {id:item.id,name:item.name}
              })
              this.team_options.unshift({id:"",name:"无"})
            }
          )
        }else{
          this.team_options = [{id:"",name:"无"}]
        }
      },
      deep:true
    },
  },
  methods: {
    confirm() {
      let data = {
        ...this.form,
        department_id: this.department_id,
        team_id: this.team_id,
        roles: this.roles,
        shops: this.shops,
        partners_orders: this.partners_orders,
        partners_system_products: this.partners_system_products
      }

      console.log(data)
      
      adminModifyData(data).then(
        res => {
          message.success(res.msg)
          this.$emit('update', false);

        }
      )
    },
    cancel() {
      this.$emit('cancel', false);
    },

    // 控制用户角色
    addRoles(){
      this.roles.push({
        "id":""
      })
    },
    delRoles(index){
      this.roles.splice(index,1)
    },
    // 控制用户店铺
    addShops(){
      this.shops.push({
        "id":""
      })
    },
    delShops(index){
      this.shops.splice(index,1)
    },
    // 控制 ozon订单管理小伙伴
    addPartnerOrders(){
      this.partners_orders.push({
        "id":""
      })
    },
    delPartnerOrders(index){
      this.partners_orders.splice(index,1)
    },
    // 控制 系统内商品小伙伴
    addPartnerSystemProducts(){
      this.partners_system_products.push({
        "id":""
      })
    },
    delPartnerSystemProducts(index){
      this.partners_system_products.splice(index,1)
    },
    // 部门切换 更新小组可选项
    department_change(){
      this.team_id = ""
      getDataFromDepartment({"department_id":this.department_id}).then(
        res =>{
          this.team_options = res.data.data.map(item => {
            return {id:item.id,name:item.name}
          })
        }
      )
    }
  },
  mounted(){
    // 更新可选店铺选项
    getShopData({"start":0,"limit":100000,"keyWord":""}).then(
      res =>{
        this.shop_options = res.data.data.map(item => {
          return {id:item.id,name:item.name}
        })
      }
    )
    // 可选人选项
    getUserData({"start":0,"limit":100000,"keyWord":""}).then(
      res =>{
        this.user_options = res.data.data.map(item => {
          return {id:item.id,name:item.username}
        })
      }
    )

    // 可选部门选项
    getDepartmentData({"start":0,"limit":100000,"keyWord":""}).then(
      res =>{
        this.department_options = res.data.data.map(item => {
          return {id:item.id,name:item.name}
        })
        this.department_options.unshift({id:"",name:"无"})
      }
    )
  }
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns {
  display: flex;
  margin-right: 30px;
  align-items: center
}

.row .columns .label{
  width: 100px;
  margin-right: 5px;
}

.row .columns .value{
  margin-right: 5px;
  color: green;
}
</style>